"use client"
import useMediaQuery from "@/hooks/useMediaQuery"
import MobileHeader from "@/components/header/MobileHeader"
import DesktopHeader from "@/components/header/DesktopHeader"
import {useEffect, useRef, useState} from "react"

export default function Header({ groups = [] }) {
    const headerRef = useRef(null);
    const [headerHeight, setHeaderHeight] = useState(0);
    const [isSticky, setIsSticky] = useState(false)
    const [isVisible, setIsVisible] = useState(true)
    const [lastScrollY, setLastScrollY] = useState(0)
    const isMobile = useMediaQuery("(max-width: 767px)")

    const visitedUrls = [
        {
            title: "Trang chủ",
            url: "/",
        },
        {
            title: "Giới thiệu",
            url: "/about",
        },
        {
            title: "Tin tức",
            url: "/news",
        },
        {
            title: "Sản phẩm",
            url: "/products",
        },
    ]
    useEffect(() => {
        if (headerRef.current) {
            const height = headerRef.current.getBoundingClientRect().height;
            setHeaderHeight(height);
        }
    }, []);
    const handleScroll = () => {
        const currentScrollY = window.scrollY

        // Xác định header có sticky hay không
        if (currentScrollY > headerHeight) {
            setIsSticky(true)
        } else {
            setIsSticky(false)
        }

        // // Xác định hướng cuộn và ẩn/hiện header
        // if (currentScrollY > (lastScrollY+5) && currentScrollY > 150) {
        //     // Cuộn xuống và đã cuộn quá 200px, ẩn header
        //     setIsVisible(false)
        // } else if (currentScrollY < (lastScrollY-5)) {
        //     // Cuộn lên, hiện header
        //     setIsVisible(true)
        // }
        if(currentScrollY >headerHeight){
            setIsVisible(true)
        }

        setLastScrollY(currentScrollY)
    }

    useEffect(() => {
        window.addEventListener("scroll", handleScroll)
        return () => {
            window.removeEventListener("scroll", handleScroll)
        }
    }, [lastScrollY])

    return (
        <div
            ref={headerRef}
            className={`w-full transition-all duration-300 ease-in-out z-[9999]
            ${isSticky ? "fixed  left-0 right-0 shadow-md" : "relative"} 
            ${isVisible ? "top-0" : "-translate-y-full"}`}
        >

            {!isMobile ? (
                <DesktopHeader groups={groups} visitedUrls={visitedUrls} />
            ) : (
                <MobileHeader groups={groups} visitedUrls={visitedUrls} />
            )}
        </div>
    )
}
